import { createRouter, createWebHistory } from 'vue-router'

const Home = () => import('../views/Home.vue')
const WebCodeEdit = () => import('../views/WebCodeEdit.vue')
const Articles = () => import('../views/Articles.vue')
const ArticleEdit = () => import('../views/ArticleEdit.vue')
const Content = () => import('../views/Content.vue')
const DirectionEdit = () => import('../views/DirectionEdit.vue')

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      title: '首页',
      keepAlive: true
    }
  },
  {
    path: '/webcodeedit',
    name: 'WebCodeEdit',
    component: WebCodeEdit,
    meta: {
      title: '代码编辑页面',
      keepAlive: false
    }
  },
  {
    path: '/articleedit',
    name: 'ArticleEdit',
    component: ArticleEdit,
    meta: {
      title: '文章编辑页面',
      keepAlive: false
    }
  },
  {
    path: '/directionedit',
    name: 'DirectionEdit',
    component: DirectionEdit,
    meta: {
      title: '文章描述页面编辑',
      keepAlive: false
    }
  },
  {
    path: '/content',
    name: 'Content',
    component: Content,
    children: [
      {
        path: '',
        redirect: to => {
          to.params.sortname = 'article'
          return '/content/article'
        }
      },
      {
        path: ':sortname',
        component: Articles,
        meta: {
          title: '内容页面',
          keepAlive: true
        }
      }
    ],
    meta: {
      title: '内容页面',
      keepAlive: true
    }
  }
]
// vue-router默认是hash模式，在hash模式下，是会有#号在URL上的
const router = createRouter({
  linkActiveClass: 'active',
  history: createWebHistory(),
  routes
})

// 前置钩子(hook)
router.beforeEach((to, from, next) => {
  if (to.params.sortname) {
    document.title = to.matched[0].meta.title + '-' + to.params.sortname
  } else {
    document.title = to.matched[0].meta.title
  }
  next()
})

export default router
